@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
html,body,#root{height:100%;margin:0px;padding:0px}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{height:100%}

.text-grid-area::-webkit-scrollbar{width:8px;height:8px}.text-grid-area::-webkit-scrollbar-track{background-color:#1e1e1e}.text-grid-area::-webkit-scrollbar-thumb{background:rgba(255,255,255,0.1);border-radius:10px}.text-grid-area::-webkit-scrollbar-thumb:hover{background:rgba(255,255,255,0.2)}.text-grid-area:focus{outline:none}.text-grid-container .terminal-base{display:block;width:100%}.text-grid-area{width:100%;height:100%;background-color:#1e1e1e;position:relative;overflow:auto;display:flex;flex:1 1}.text-grid-status-bar{box-sizing:content-box;display:flex;background-color:#383838;color:white;border:1px solid #1e1e1e;width:100%;height:24px;line-height:24px;font-size:smaller;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.text-grid-status-bar ul{display:block;padding:0px;margin:0px}.text-grid-status-bar ul li{display:inline-block;margin-left:10px}.text-grid{box-sizing:content-box;position:-webkit-sticky;position:sticky;left:0px;top:0px;border:1px solid #252526;color:#dcdcaa;background-color:#1e1e1e;width:100%;height:100%;overflow:hidden;line-height:0px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-size:smaller;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.text-grid :focus{outline:none}.text-grid .text-grid-selection{box-sizing:border-box;border:1px solid rgba(220,220,170,0.5);background-color:rgba(255,255,255,0.1);position:absolute;display:inline-block}.text-grid .text-grid-row{box-sizing:border-box;display:flex;flex-direction:row;overflow:hidden;width:100%;white-space:nowrap;margin-top:-1px}.text-grid .text-grid-row .text-grid-cell{box-sizing:border-box;border:1px solid #252526;display:flex;justify-content:center;flex-direction:column;height:100%;margin-left:-1px;overflow:hidden;text-align:center;vertical-align:middle}.text-grid .text-grid-row .row-hover{background-color:#252526}.text-grid .text-grid-row .col-hover{background-color:#252526}.text-grid .text-grid-row .cell-hover{background-color:#383838}.text-grid .text-grid-row .active-override{border-left:1px solid #9cdcfe;background-color:#383838;color:#9cdcfe}.text-grid .text-grid-row .active-insert{border-left:1px solid #9cdcfe}.text-grid-console{padding:10px;border-top:1px solid #252526;background:#171717}.text-grid-console textarea{outline:none;resize:none;overflow:auto;width:100%;height:100%;border:0px solid #888;background:#171717;color:#dcdcaa;padding:10px;font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

